.theme-button {
  border: none;
  padding: 0.7rem 1.2rem;
  font-size: 1rem;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.2);
  color: white;
}

.theme-button.active {
  background-color: black;
  color: white;
}

@media (max-width: 480px){
  .theme-button{
    font-size: 1.2rem;
  }
}
