* {
    padding:0;
    margin:0;
    box-sizing: border-box;
    font-size: 16px;
}

body {
    background-image: linear-gradient(to bottom , #444, #2137);
    color: white;
    font-family: 'Times New Roman', Times, serif;
    display: flex;
    flex-direction: column;
    min-height:100vh;
    overflow-x: hidden;
  }

.nav-link {
    color:white;
    text-decoration: none;
    width:33.3%;
}

.nav-items {
    display:flex;
    justify-content: space-around;
    list-style-type: none;
    width:80%;
    align-items: center;
    font-weight:700;
    font-size: large;
}

li:hover {
    transform: scale(1.2);
    text-shadow: 1px 1px 1px black;
    transition: 0.2s;
}

.nav-pc {
    display: flex;
    flex-direction: row;
    background-image: linear-gradient(to right, cornflowerblue, dodgerblue);
    width:100%;
    transition: 0.7s;
    position:absolute;
    z-index: 1;
}

.nav-logo-container {
    width:11rem;
    display:flex;
}

.nav-logo-container:hover {
    transform: scale(1.1);
    transition: 0.3s;
}

.footer-logo-container {
    display: flex;
    width:25%;
}

img {
    width:100%;
}

.flex-row {
    display: flex;
   flex-wrap: wrap;
}

.just-cont-sbet {
    justify-content: space-between;
}

.aln-items-center {
    align-items: center;
}

.just-cont-center {
    justify-content: center;
}

.flex-collumn {
    display: flex;
    flex-direction:column;
}

.w-25 {
    width:25%;
}

.w-50 {
    width:50%;
}

.w-75 {
    width:75%;
}

.w-100 {
    width:100%;
}

.p10 {
    padding:1rem;
}

.mt {
    margin-top:0.7rem;
}

p {
    text-align: left;
    font-size: 1.2rem;
}

.img-fluid {
    max-width:70%;
}

.img-container {
    display: flex;
    justify-content: center;
}

h2 {
    padding:0.5rem;
    font-size: 1.5rem;
}

h1{
    text-align: center;
    font-size: 7rem;
    -webkit-text-stroke: 2px black;
}

main {
    flex:1;
}

@media (max-width: 768px) {
    * {
      font-size: 14px;
    }
    
    .w-50 {
        width:100%;
    }
    .w-25 {
        width:100%;
    }

    .footer-med {
        flex-direction: column;
        flex:1;
        flex-wrap: wrap;     
    }

    .mobile-visible {
        top: 0;
        transition: 0.7s;
        font-size: 1.6rem;
    }
    
    .nav-link {
        font-size:1.5rem;
    }
    .link {
        font-size: 1.5rem;
    }

    .footer-logo-container {
        width:100%;
        justify-content: center;
    }

    .footer-logo-container img {
        width:50%;
    }
    .nav-pc {
        position: relative;
        flex-direction: column;
        height: auto;
        top: 0;
        align-items: center;
      }
      
      .nav-logo-container {
        justify-content: center;
        display:flex;
      }
      
      .nav-logo-container img{
        max-width: 80%;
      }
    
      .nav-items {
        display: flex;
        flex-direction: column;
        align-items: center;
      }
    
      .nav-link {
        width: auto;
        margin-right:0;
        margin-bottom: 0.5rem;
      }

      .link{
        margin-left:auto;
      }
    
      .img-container img{
        max-width: 80%;
      }
  }
  
  @media (max-width: 480px) {
    * {
      font-size: 9px;
    }

    .img-container img {
        max-width: 60%;
    }

  }

.footer-med {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    align-items: center;
    background-image: linear-gradient(to right, cornflowerblue, dodgerblue);
}

.copyright {
    text-align: right;
    font-size:0.9rem;
}

.input-grey {
    background-color: grey;
    border: none;
    color:white;
}

.input-grey:focus {
    outline:none;
    box-shadow: 0px 0px 10px 0px lightgrey;
    transition: 0.3s;
}

.input-grey::placeholder {
    color:white;
    text-align: center;
}

.label-email {
    font-size: 1.2rem;
    text-decoration:underline;
    cursor: pointer;
}

.input-email {
    border-radius:5px;
    height:1.5rem;
}

.button-confirm-newsletter {
    border:none;
    background-color:grey;
    color:white;
    width:7rem;
    height:1.7rem;
    border-radius:10px;
}

.button-confirm-newsletter:hover {
    transform: scale(1.1);
    box-shadow: 0px 0px 10px 0px lightgrey;
    transition:0.3s;
    cursor:pointer;
}

.link {
    color:#444;
    text-decoration:none;
}

.carousel {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
    position: relative;
  }
  
  .carousel-text {
    color: #333;
    font-size: 24px;
    position: relative;
  }
  
  .carousel-image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    z-index: -1;
  }
