.cookie-modal {
  background-color: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 20px;
  color: #2137;
  padding: 20px;
  position: fixed;
  bottom: 20px;
  right: 20px;
  z-index: 9999;
  transition: opacity 0.5s;
  width: 300px; /* Adjust the width as needed */
  max-width: 90%; /* Add a maximum width to ensure responsiveness */
}

.cookie-modal.fade-out {
  opacity: 0; /* Make the modal transparent */
}

.cookie-modal h2 {
  font-size: 18px;
  margin-bottom: 10px;
  text-align: center; /* Center align the heading */
}

.cookie-modal p {
  margin-bottom: 20px;
  text-align: center; /* Center align the text */
}

.cookie-modal .accept-btn {
  background-color: green;
  color: #fff;
}

.cookie-modal .deny-btn {
  background-color: red;
  color: #fff;
}

.cookie-modal .read-more-btn {
  background-color: dodgerblue;
  color: #fff;
}

.cookie-modal button {
  font-size: 0.9rem;
  border-radius: 50px;
  border: none;
  padding: 6px 12px;
  cursor: pointer;
  display: inline-block; /* Display the buttons inline-block instead of block */
  margin: 0 5px; /* Add margin to create spacing between the buttons */
}

.cookie-modal button:hover {
  opacity:0.8;
}

.cookie-modal button a {
  text-decoration: none;
  color: white;
  font-size: 0.9rem;
}

.cookie-img {
  width: 30%;
  margin: 0 auto; /* Center align the image */
  display: flex;
  justify-content: center;
  align-items: center;
}

@media (max-width: 768px){
  .cookie-modal button, a{
    font-size: 1rem;
  }
  .cookie-modal button a{
    font-size: 1rem;
  }
}

@media (max-width: 480px){
  .cookie-modal button {
    font-size: 1.6rem;
  }
  .cookie-modal button a {
    font-size: 1.6rem;
  }
}

@media (max-width: 325px){
  .cookie-modal button {
    font-size: 1.5rem;
  }
  .cookie-modal button a {
    font-size: 1.5rem;
  }
}